body {
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.board {
  display: block;
  margin-top: 10px;
  position: relative;
  left: 50%;
  margin-left: -150px;
  background-color: #d4edda;
  border: black !important;
}

h1 {
  margin-top: 5%;
  font-size: 2em !important;
}

.alert {
  margin: 2% 10%;
}

.controlButtons .btn-up,
.controlButtons .btn-right,
.controlButtons .btn-left,
.controlButtons .btn-down {
  width: 16%;
  text-align: center;
  border-color: black;
}
.controlButtons .btn-up,
.controlButtons .btn-down {
  display: block;
  margin-left: 42%;
}
.controlButtons .btn-right,
.controlButtons .btn-lef {
  display: inline-block;
}
.desktopHide {
  display: none;
}
@media (max-width: 800px) {
  .mobileShow {
    display: block;
  }
}

